import axios from "axios";
import React, { useEffect, useState, useReducer } from "react";
import { Card, Col, Navbar, Row, Container, Nav } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import Swal from "sweetalert2";

function handle(state, action) {
  switch (action.type) {
    case "add":
      return {
        ...state,
        items: [...state.items, action.product],
        totalprice: state.totalprice + action.product.price,
      };
    case "del":
      return {
        items: state.items.filter((_, index) => index !== action.index),
        totalprice: state.totalprice - state.items[action.index].price,
      };
    default:
      return state;
  }
}

function UseeffectAPI() {
  const [state, dispatch] = useReducer(handle, { items: [], totalprice: 0 });
  const [products, setProducts] = useState([]);
  const [isLanding, setLanding] = useState(true);

  function getApi() {
    axios.get("https://fakestoreapi.com/products").then((res) => {
      setProducts(res.data);
    });
  }

  useEffect(() => {
    getApi();
  }, []);
  console.log(state.items);

  function cartClick() {
    setLanding(false);
  }

  const handleAddToCart = (product) => {
    dispatch({ type: "add", product });
    Swal.fire({
      title: "Added to Cart!",
      text: `${product.title.slice(0, 30)} has been added to your cart.`,
      icon: "success",
      timer: 1500,
      showConfirmButton: false,
    });
  };

  return (
    <div>
      <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
        <Container>
          <Navbar.Brand href="/">MY Fashion</Navbar.Brand>
          <Nav className="ms-auto">
            <Nav.Link
              href="/useeffectapi"
              className={window.location.pathname === "/useeffectapi" ? "active" : ""}
            >
              Home
            </Nav.Link>
            <Nav.Link
              href="/about"
              className={window.location.pathname === "/about" ? "active" : ""}
            >
              About
            </Nav.Link>
            <Nav.Link
              href="/contact"
              className={
                window.location.pathname === "/contact" ? "active" : ""
              }
            >
              Contact
            </Nav.Link>
            <Nav.Link
              href="/feedback"
              className={
                window.location.pathname === "/feedback" ? "active" : ""
              }
            >
              Feedback
            </Nav.Link>
            <Nav.Link href="/login" className="lgt">
              Logout
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      <div className="bg p-4">
        <div>
          {isLanding ? (
            <button className="act" onClick={cartClick}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-cart-check"
                viewBox="0 0 16 16"
              >
                <path d="M11.354 6.354a.5.5 0 0 0-.708-.708L8 8.293 6.854 7.146a.5.5 0 1 0-.708.708l1.5 1.5a.5.5 0 0 0 .708 0z" />
                <path d="M.5 1a.5.5 0 0 0 0 1h1.11l.401 1.607 1.498 7.985A.5.5 0 0 0 4 12h1a2 2 0 1 0 0 4 2 2 0 0 0 0-4h7a2 2 0 1 0 0 4 2 2 0 0 0 0-4h1a.5.5 0 0 0 .491-.408l1.5-8A.5.5 0 0 0 14.5 3H2.89l-.405-1.621A.5.5 0 0 0 2 1zm3.915 10L3.102 4h10.796l-1.313 7zM6 14a1 1 0 1 1-2 0 1 1 0 0 1 2 0m7 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0" />
              </svg>{" "}
              Cart
            </button>
          ) : (
            <div className="bbtn">
              <button
                className="btn btn-primary"
                onClick={() => setLanding(true)}
              >
                Back
              </button>
            </div>
          )}
        </div>

        <Row>
          {isLanding &&
            products.map((product) => (
              <Col lg={4} md={6} sm={12} className="mb-4" key={product.id}>
                <Card>
                  <Card.Img className="img" src={product.image} />
                  <Card.Body>
                    <br />
                    <Card.Title>{product.title.slice(0, 30)}</Card.Title>
                    <Card.Text>
                      <b>Category:</b> {product.category}
                      <br />
                      <b>Description:</b> {product.description.slice(0, 70)}
                      <br />
                      <b>Price:</b> {product.price}
                      <br />
                      <b>Rating:</b> {product.rating.rate} (
                      {product.rating.count} reviews)
                    </Card.Text>
                    <div className="cbtn">
                      <button
                        className="btn btn-primary"
                        onClick={() => handleAddToCart(product)}
                      >
                        Add to Cart
                      </button>
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            ))}
        </Row>

        {!isLanding && (
          <div className="cdiv0">
            <h1>Your Cart</h1>
            <h2>Total Price: {state.totalprice}</h2>
            <Row>
              {state.items.map((i, index) => (
                <Col lg={4} md={6} sm={12} key={index}>
                  <Card>
                    <Card.Img className="img" src={i.image} />
                    <Card.Body>
                      <br />
                      <Card.Title>{i.title.slice(0, 30)}</Card.Title>
                      <Card.Text>
                        <b>Category:</b> {i.category}
                        <br />
                        <b>Description:</b> {i.description.slice(0, 70)}
                        <br />
                        <b>Price:</b> {i.price}
                        <br />
                        <b>Rating:</b> {i.rating.rate} ({i.rating.count}{" "}
                        reviews)
                      </Card.Text>
                      <button
                        className="btn btn-primary"
                        onClick={() => dispatch({ type: "del", index })}
                      >
                        Remove
                      </button>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          </div>
        )}
      </div>
      <footer className="bg-dark text-light py-3 mt-auto">
        <Container>
          <Row>
            <Col className="text-center">
              &copy; {new Date().getFullYear()} MY Fashion. All Rights Reserved.
            </Col>
          </Row>
        </Container>
      </footer>
    </div>
  );
}

export default UseeffectAPI;
